<template>
  <div class="hc-integral">
    <mt-header title="我的积分" class="hc-f14 hc-border-bottom hc-header-fix">
      <div @click="$router.back()" slot="left">
        <mt-button icon="back"></mt-button>
      </div>
    </mt-header>
    <div class="hc-integral-header hc-pr">
      <div class="hc-inte-text hc-f16">可用积分</div>
      <div class="hc-inte-num">{{curPoint}}</div>
      <div class="hc-inte-bottom hc-pa">
        <div class="hc-col-6 hc-right-border">
          <div class="hc-bottom-text hc-one-in hc-f16">{{totalPoint}}</div>
          <div class="hc-bottom-num">累计积分</div>
        </div>
        <div class="hc-col-6">
          <div class="hc-bottom-text hc-one-in hc-f16">{{totalExchange}}</div>
          <div class="hc-bottom-num">累计兑换</div>
        </div>
      </div>
    </div>
    <div class="hc-integral-content" v-infinite-scroll="loadMore" infinite-scroll-disabled="moreLoading" infinite-scroll-distance="0" infinite-scroll-immediate-check="'false'">
      <div v-for="(item, index) in integralArr" :key="index" class="hc-inte-item">
        <div class="hc-item-img">
          <icon value="ziyuan11" color="78d7ab"/>
        </div>
        <div class="hc-item-text">
          <div class="hc-f14 hc-one-in">2018-08-03 16:44:40</div>
          <div class="hc-one-in">{{item.tradeDetail}}</div>
        </div>
        <div class="hc-item-inte hc-pa hc-f14">
          {{item.tradePoint}}积分
        </div>
      </div>
      <div class="hc-text-center" v-show="integralArr.length<=0">
        暂无数据
      </div>
      <div class="hc-text-center hc-loading" v-show="queryLoading">
        <mt-spinner type="fading-circle" color="#00ccff" :size="20" v-show="!allLoaded&&queryLoading"></mt-spinner>
        <span class="hc-loaded" v-show="allLoaded">已全部加载</span>
      </div>
    </div>
  </div>
</template>

<script>
import { getPointByUser } from '../../api/axios.js'
export default {
  data() {
    return {
      moreLoading: false, // 若为真，则无限滚动不会被触发
      queryLoading: false,
      allLoaded: false,
      integralArr: [],
      currentPage: 0,
      totalPage: 3,//假设总页数3
      curPoint: 0,
      totalPoint: 0,
      totalExchange: 0,
      integralText: []
    }
  },
  methods: {
    loadMore() {
      // loading显示
      this.queryLoading = true;
      // 阻止下拉加载
      this.moreLoading = true;
      this.getPointByUser();
    },
    getPointByUser() {
      this.currentPage += 1;
      getPointByUser({
        pageSize: 10,
        pageNum: this.currentPage
      }).then((result) => {
        console.log(result, '111111111111')
        this.integralArr = this.integralArr.concat(result.result.list);
        this.curPoint = result.result.curPoint;
        this.totalPoint = result.result.totalPoint;
        this.totalExchange = result.result.totalExchange;
        // 加载完毕关闭loading
        if(this.currentPage < this.totalPage) {
          // 加载完毕开启下拉加载
          this.queryLoading = false;
          this.moreLoading = false;
        }else {
          this.allLoaded = true;
        }
      })
    }
  },
  mounted() {
    this.getPointByUser();
  }
}
</script>

<style lang="scss">
body {
  background-color: #fff;
}
.hc-integral {
  background-color: #fff;
  .mint-spinner-fading-circle {
    margin: 10px auto;
  }
  padding-top: 48px;
  .hc-integral-header {
    color: #fff;
    text-align: center;
    width: 100%;
    height: px2rem(155);
    // background-color: #000;
    background-image: linear-gradient(56deg,#ffcd59 0%,#f30c51 100%);
    .hc-inte-text {
      padding-top: px2rem(25);
    }
    .hc-inte-num {
      font-size: 30px;
    }
    .hc-inte-bottom {
      width: 100%;
      height: px2rem(60);
      // background-color: #fff;
      background-color: rgba($color: #ffffff, $alpha: 0.2);
      bottom: 0px;
      left: 0px;
      .hc-right-border {
        position: relative;
        &::after {
          content: "";
          display: inline-block;
          position: absolute;
          width: 1px;
          height: 40px;
          background-color: rgba($color: #ffffff, $alpha: 0.8);
          right: 0px;
          top: 0px;
          bottom: 0px;
          margin: auto;
        }
      }
      .hc-col-6 {
        padding: 0px 10px;
        height: 100%;
      }
      .hc-bottom-text {
        padding-top: px2rem(12);
      }
    }
  }
  .hc-integral-content {
    .hc-loaded {
      line-height: 30px;
    }
    margin-top: 20px;
    width: 100%;
    background-color: #fff;
    .hc-inte-item {
      border-bottom: 1px solid #dbdbdb;
      padding: 22px px2rem(15);
      display: flex;
      align-items: center;
    }
    .hc-item-img {
      width: 30px;
      height: 30px;
      line-height: 0px;
      // background-color: #000;
      font-size: 30px;
      border-radius: 50%;
    }
    .hc-item-text {
      padding-left: 10px;
    }
    .hc-item-inte {
      right: px2rem(15);
      color: #ff784e;
    }
  }
}
</style>



